import React from 'react';

function test(prop){
	console.log(`${prop.cheeseitemName}`);
	prop.click(prop.cheese);
}

class CheeseComponent extends React.Component {

	render() {

		return(
			<tr>
				<td 
					onClick={()=>{test(this.props)}}
				>
					-{this.props.cheese.producer} <br/>
					-{this.props.cheese.itemName} <br/>
					{this.props.cheese.plu}
				</td>
			</tr>
		)
	}
}

export default CheeseComponent;